@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Fredoka:wght@300;400;500&family=Saira+Condensed:wght@400;700&display=swap);
html, body
{
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden; 
  background-color: #212529;
}

h1, h2, h3, h4, h5, p, nav, form, .btn {
  font-family: 'Saira Condensed', sans-serif;
}

span {
  font-weight: bold;
}

.intro {
  font-size: larger;
}

.home-logo {
  height: 18rem;
}

.home-logo:hover {
  animation: spin infinite .5s linear reverse;
}

.jumbotron {
  background-image: url(/static/media/jumbo-bg.0a4a8e2d.jpeg);
  background-size: cover;
  background-position: top;
  height: 625px;
}

.about {
  background-image: url(/static/media/about-bg.f2d4f5c9.jpeg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-height: 625px;
}

.contact {
  background-image: url(/static/media/contact-bg.40090d56.jpeg);
  background-size: cover;
  background-position: center;
  height: 625px;
}

.login {
  background-image: url(/static/media/login-bg.f1fe5b15.jpeg);
  background-size: cover;
  background-position: center;
  height: 625px;
}

.gallery {
  background-image: url(/static/media/gallery-bg.e32dd4d9.jpeg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.favorites {
  background-image: url(/static/media/favorites-bg.9ff5dede.jpeg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.noMatch {
  background-image: url(/static/media/404-bg.3aed606a.jpeg);
  background-size: cover;
  background-position: center;
  height: 625px;
}

.noMatch-bg {
  width: 100%;
  height: 40%;
  background-color: #fdc334;
}

.not-found {
  font-weight: bolder;
  font-size: 3rem;
}

.card {
  width: 18rem;
  height: 100%;
}

.card-img-top {
  width: 100%;
  object-fit: scale-down;
}

.gallery-img {
  height: 20vw;
}

.favorite-img {
  height: 25vw;
}

.detail-img {
  height: auto;
  width: 95%;
}

.detail-text {
  font-size: large;
}

.backBtn {
  text-decoration: none;
  font-weight: bold;
  font-size: 1.2rem;
}

.likeBtn {
  font-size: 2rem;
}

.likeBtn button {
  background: none;
  border: none;
}

.spinner {
  -webkit-animation: spin infinite 2s linear;
          animation: spin infinite 2s linear;
  font-size: 3rem;
}

.likes {
  min-height: 625px;
}

@-webkit-keyframes spin {
from {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
to {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
}

@keyframes spin {
from {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
to {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
}

@media screen and (max-width: 768px) {
  .card {
    width: 95%;
    height: 100%;

  }

  .gallery-img {
    height: 50%;
  }

  .favorite-img {
    height: 50%;
  }
}

