.cart {
  position: fixed;
  bottom: 0;
  right: 0;
  min-width: 30%;
  max-width: 30%;
  max-height: 80%;
  overflow: auto;
  padding: .5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  border-top-left-radius: .5rem;
}

.cart span {
  cursor: pointer; 
}

.cart .close {
  position: absolute;
  top: .8rem;
  right: .8rem;
  cursor: pointer;
}

.cart-closed {
  position: fixed;
  bottom: 5.5%;
  right: 3.3%;
  font-size: 2rem;
  cursor: pointer;
  background-color: #363d44;
  border-radius: 10px;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
}

.cart-closed span {
  margin-left: 5px;
}

.cart-closed:hover {
  -webkit-transform: scale();
          transform: scale();
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}

@media screen and (max-width: 768px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
    max-width: 50%;

  }
}
